.document-overlay-container {
    position: absolute;
    width: 80vw;
    max-width: 80vw;
    z-index: 99999;
    top: 0px;
    left: 0px;
    right: 20vw;
    bottom: 0px;
    pointer-events: none;
    font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
}

.document-overlay {
    position: absolute;
    left: 50%;
    transform: translate(-50%, -0%);
    bottom: 20px;
    z-index: 100000;
    width: 65%;
    max-width: 65%;
    user-select: none;
    pointer-events: all;
}

.bg-grey {
    background-color: rgba(0, 0, 0, 0.05);
}