.call-participant {
    flex-basis: 0;
    flex-shrink: 1;
    flex-grow: 1;
    max-height: calc(calc(20vw - 20px) * .75);
    margin-left: 10px;
    margin-right: 10px;
    max-width: calc(100% - 20px);
}

.call-participant-name {
    background-color: black;
    position: absolute;
    left: 6px;
    bottom: 0px;
    z-index: 100002;
}

.call-participant-name > h5 {
    color: white;
    margin: 2px;
}

.call-participant-name > span {
    margin: 0px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 2px 2px;
    position: absolute;
    display: none;
}

.cursor-pointer {
    cursor: pointer;
}

.call-participant-icon {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 100002;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 4px;
}

.call-participant-icon img {
    display: block;
    height: 24px;
    width: 36px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 4px 4px;
}

.call-participant-icon-tooltip {
    margin: 0px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 2px 2px;
}

.call-participant-icon span {
    display: none;
}

.call-participant-icon:hover span {
    display: inline;
}

.reaction-overlay {
    position: absolute;
    left: 8px;
    top: 8px;
    z-index: 100002;
}

.call-participant-video-container {
    margin-left: auto;
    margin-right: auto;
    position: relative;
    display: inline-block;
    height: calc(100% - 4px);
    
}

.pause-buttons {
    position: absolute;
    top: 8px;
    left: 8px;
    z-index: 100002;
    height: auto;
}

.pause-buttons img {
    height: 24px;
}

.pause-buttons button {
    padding: 4px 8px;
    margin-right: 4px;
}

.pause-buttons > span {
    margin: 0px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 2px 2px;
    display: none;
}

.pause-buttons:hover > span {
    display: block;
}

.four-x-three {
    opacity: 0;
    height: 100%;
}

.call-participant-video {
    position: absolute;
    left: 1px;
    top: 1px;
    right: 0px;
    bottom: 0px;
    height: 100%;
    width: 100%;
    border-radius: 8px;
    border: 1px rgba(0, 0, 0, 0.2) solid;
    box-shadow: 1px 2px 0px rgba(60,64,67,.30),0px 1px 3px 1px rgba(60,64,67,.15);
    z-index: 100001;
}

.call-participant-speaking .call-participant-video {
    border: 1px rgb(172, 255, 47) solid;
    box-shadow: 1px 2px 0px rgba(172, 255, 47,.30),0px 1px 3px 1px rgba(172, 255, 47,.15);
}