.pushing-overlay {
    height: 100%;
    width: 100%;
    pointer-events: none;
}

.pushing-overlay-top-crop-height {
    height: calc(14% - 1px);
}

.pushing-overlay-full-height {
    position: relative;
    height: 72%;
}

.pushing-overlay-bottom-crop-height {
    height: calc(14% - 1px);
}

.pushing-overlay-top {
    position: relative;
}

.pushing-overlay-toolbar {
    position: absolute;
    width: 100%;
    bottom: 0px;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    gap: 8px;
}

.pushing-overlay h3 {
    margin: 0px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 5px 12px;
}

.pushing-overlay .viewer-span {
    margin: 0px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 5px 12px;
}

.pushing-overlay-visible-window {
    border-top: 1px #1a73e8 dotted;
    border-bottom: 1px #1a73e8 dotted;
}

.bg-grey {
    background-color: rgba(0, 0, 0, 0.05);
}