.as-position-indicator {
    background-color: white;
    border-radius: 8px;
    border: 1px rgba(0, 0, 0, 0.2) solid;
    box-shadow: rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    padding: 8px;
    display: flex;
    flex-direction: row;
    gap: 4px;
    align-items: center;
}

.as-position-indicator-icon {
    flex-shrink: 0;
    position: relative;
    width: 54px;
}

.as-position-indicator-icon>img {
    height: 100%;
    width: 100%;
    object-fit: contain;
}

.as-position-indicator-text {
    font-size: 14px;
    font-weight: 300;
    margin-bottom: 4px;
}

.as-position-indicator-tip {
    font-size: 14px;
    font-weight: 300;
    color: rgb(175, 175, 175);
}

.as-position-indicator-green {
    border: 1px rgb(15, 157, 88) solid;
    box-shadow: rgba(15, 157, 88, 0.15) 0px 2px 6px 2px
}

.as-position-indicator-red {
    border: 1px rgb(233, 66, 53) solid;
    box-shadow: rgba(233, 66, 53, 0.15) 0px 2px 6px 2px
}