.google-doc {
    height: 100vh;
    max-height: 100vh;
    display: flex;
    flex-direction: column;
    width: 80%;
}

.google-doc-header > img {
    width: 100%;
    max-width: 1206px;
}

.google-doc-pages {
    background-color: rgb(248, 249, 250);
    padding: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    overflow-y: scroll;
    align-items: center;
}

.google-doc-pages > img {
    max-width: 900px;
    width: 90%;
}