.telecursor-overlay {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    z-index: 100002;
}

.mtrail-element {
    position: absolute;
    height: 8px;
    width: 8px;
    border-radius: 4px;
    z-index: 100002;
    opacity: 0;
}

@keyframes mtrail-fading-keyframes {0% {opacity:0}50%{opacity:1} 100%{opacity:0}}
.mtrail-fading  {animation: .3s mtrail-fading-keyframes }

.telecursor-overlay .reaction {
    position: absolute;
    top: 8px;
    left: 8px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 5px 12px;
    font-size: 16px;
}

@keyframes reaction-fading-keyframes {0% {opacity:0}10%{opacity:1}90%{opacity:1}100%{opacity:0}}
.telecursor-overlay .reaction-fading  {animation: 3s reaction-fading-keyframes }