.App {
  display: flex;
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
}

.App button, .document-overlay-container button {
  font-family: "Google Sans", Roboto, RobotoDraft, Helvetica, Arial, sans-serif;
  background-color: #1a73e8;
  border: none;
  color: #fff;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 4px;
  cursor: pointer;
}

.App button:hover, .document-overlay-container button:hover {
  background-color: #2b7de9;
  box-shadow: 0 1px 3px 1px rgb(66 133 244 / 15%);
}

.App button.button-red, .document-overlay-container button.button-red {
  background-color: #ea4335;
}

.App button.button-red:hover, .document-overlay-container button.button-red:hover {
  background-color: #E44E42;
}

.App button[disabled], .document-overlay-container button[disabled] {
  background-color: #777777;
  color: rgb(194, 194, 194);
}

.instructions {
  position: absolute;
  max-width: 60%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 110000;
  top: 10px;
  background-color: rgb(230, 243, 255);
  border: 1px solid rgb(105, 181, 252);
  padding: 8px;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  gap: 8px;
}