.pushpeek {
    position: relative;
    background-color: white;
    border: 1px #1a73e8 dotted;
    overflow: hidden;
    box-shadow: rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    margin-top: 8px;
}

.pushpeek img {
    position: absolute;
    top: 8px;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 100000;
}

.pushpeek-toolbar {
    position: absolute;
    left: 8px;
    top: 8px;
    width: calc(100% - 16px);
    z-index: 100003;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    pointer-events: none;
}

.pushpeek-toolbar-right {
    display: flex;
    flex-direction: row;
    gap: 8px;
    pointer-events: all;
}

.pushpeek h3 {
    margin: 0px;
    background-color: #e8eaed;
    border-radius: 4px;
    padding: 5px 12px;
}

.pushpeek-video-container {
    height: 0px;
    position: relative;
    z-index: 100001;
    transition: padding-bottom .5s;
}

.pushpeek-video-container video {
    transform: translate(0%, -15%);
    width: 125%;
}